export const addNetworkListener = (setNetwork, setStatus) => {
    if (window.ethereum) {
        window.ethereum.on("chainChanged", (chainId) => {
            setNetwork(chainId);
            setStatus("");
        });
    }
}

export const addWalletListener = (setWallet, setStatus) => {
    if (window.ethereum) {
        window.ethereum.on("accountsChanged", (accounts) => {
            if (accounts.length > 0) {
                setWallet(accounts[0]);
                setStatus(" Import your nfts.");
            } else {
                setWallet("");
                setStatus(" Connect to Metamask using the top right button.");
            }
        });
    } else {
        setStatus(
            <p>
                {" "}
                {" "}
                <a target="_blank" href={`https://metamask.io/download.html`}>
                    You must install Metamask, a virtual Ethereum wallet, in your
                    browser.
                </a>
            </p>
        );
    }
}

export const getNFTHandlerContract = (addr) => {
    if (window.ethereum) {
        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const signer = provider.getSigner();
        const tokenHandler = new Contract(
            addr,
            TokenHandler.abi,
            signer
        );

        return tokenHandler;
    }

    return null;
}

export const getHeroContract = (addr) => {
    if (window.ethereum) {
        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const signer = provider.getSigner();
        const token = new Contract(
            addr,
            Token.abi,
            signer
        );

        return token;
    }

    return null;
}

