import React, { useState, useEffect, useCallback } from 'react';
import { Unity, useUnityContext } from "react-unity-webgl";
import {
  getNFTHandlerContract,
  getCurrentState,
  getHeroContract,
  mobileCheck
} from "./utils/interact.js"

type Response = {
  result: string | null;
  error: string | null;
}

function App() {
  function handleCacheControl(url: string) {
    if (url.match(/\.data/) || url.match(/\.bundle/)) {
      return "must-revalidate";
    }
    if (url.match(/\.mp4/) || url.match(/\.wav/)) {
      return "immutable";
    }
    return "no-store";
  }
  const [devicePixelRatio, setDevicePixelRatio] = useState(
    window.devicePixelRatio
  );
  const [orientation, setOrientation] = useState('LANDSCAPE');
  const handleChangePixelRatio = useCallback(
    function () {
      // A function which will update the device pixel ratio of the Unity
      // Application to match the device pixel ratio of the browser.
      const updateDevicePixelRatio = function () {
        setDevicePixelRatio(window.devicePixelRatio);
      };
      // A media matcher which watches for changes in the device pixel ratio.
      const mediaMatcher = window.matchMedia(
        `screen and (resolution: ${devicePixelRatio}dppx)`
      );
      // Adding an event listener to the media matcher which will update the
      // device pixel ratio of the Unity Application when the device pixel
      // ratio changes.
      mediaMatcher.addEventListener("change", updateDevicePixelRatio);
      return function () {
        // Removing the event listener when the component unmounts.
        mediaMatcher.removeEventListener("change", updateDevicePixelRatio);
      };
    },
    [devicePixelRatio]
  );

  const HERO_HANDLER_CONTRACT: string = "0x3f69dE218852eB2121E6E70a564704Dc6551210D";
  const HERO_CONTRACT: string = "0x7Eaa6Bf2874661dBC0d0dA613720B7b58EB347F1";

  const buildUrl = "Build";
  const buildName = "DefaultWebGL";
  const { unityProvider, loadingProgression, isLoaded, addEventListener, removeEventListener, requestFullscreen } = useUnityContext({
    loaderUrl: buildUrl + "/{{{ LOADER_FILENAME }}}",
    dataUrl: buildUrl + "/{{{ DATA_FILENAME  }}}",
    frameworkUrl: buildUrl + "/{{{ FRAMEWORK_FILENAME  }}}",
    codeUrl: buildUrl + "/{{{ CODE_FILENAME  }}}",
    streamingAssetsUrl: "StreamingAssets",
    companyName: "{{{ COMPANY_NAME }}}",
    productName: "{{{ PRODUCT_NAME }}}",
    productVersion: "{{{ PRODUCT_VERSION }}}",


    // loaderUrl: buildUrl + "/" + buildName + ".loader.js",
    // dataUrl: buildUrl + "/" + buildName + ".data",
    // frameworkUrl: buildUrl + "/" + buildName + ".framework.js",
    // codeUrl: buildUrl + "/" + buildName + ".wasm",
    // streamingAssetsUrl: "StreamingAssets",
    // companyName: "CHAIN OF ALLIANCE LTD.",
    // productName: "Chain of Alliance",
    // productVersion: "0.1",

    cacheControl: handleCacheControl
  });

  const handleNFTRequest = useCallback(async () => {
    const state = await getCurrentState();
    console.log(state)

    let str: string = "";
    if (state.network == '97' && state.address != '') {
      let contract = getNFTHandlerContract(HERO_HANDLER_CONTRACT);

      if (contract != null) {
        const data = await contract.getOwnedTokens(state.address);
        data.map((elem: any) => (str += `hero_${Number(elem._hex)};`));
        str = str.slice(0, -1);
      }
    }
    return str;
  }, []);

  const handleNftImport = useCallback(async (accountId: string, nftId: string): Promise<string> => {
    return JSON.stringify(await handleNftImportInternal(accountId, nftId));
  }, []);

  const handleNftImportInternal = async (accountId: string, nftId: string): Promise<Response> => {
    const state = await getCurrentState();
    console.log(state)

    if (state.network != '97' || state.address == '') {
      return { result: null, error: "Wrong MetaMask config" };
    }

    let contract = getNFTHandlerContract(HERO_HANDLER_CONTRACT);
    if (contract == null) {
      return { result: null, error: "Did not find nft handler contract at " + HERO_HANDLER_CONTRACT };
    }

    const isApproved = await contract.isApprovedForAll(state.address, contract.address);
    if (isApproved == false) {
      // approve first
      const heroContract = getHeroContract(HERO_CONTRACT);

      if (heroContract == null) {
        return { result: null, error: "Did not find hero contract at " + HERO_CONTRACT };
      }

      try {
        let estimatedGas = await heroContract.estimateGas.setApprovalForAll(contract.address, true);
        let nftTxn;
        nftTxn = await heroContract.setApprovalForAll(contract.address, true, { gasLimit: estimatedGas });
        await nftTxn.wait();
      } catch {
        return { result: null, error: "Approval tx failed" };
      }
    }

    let id = nftId.split('_')[1];
    let nftTxn;
    try {
      let estimatedGas = await contract.estimateGas.lock(id, accountId);
      nftTxn = await contract.lock(id, accountId, { gasLimit: estimatedGas });

      console.log("Mining... please wait");
      await nftTxn.wait();
    } catch {
      return { result: null, error: "Import tx failed" };
    }

    return { result: nftTxn.hash as string, error: null };
  };

  useEffect(() => {
    addEventListener("getNfts", handleNFTRequest);
    return () => {
      removeEventListener("getNfts", handleNFTRequest);
    };
  }, [addEventListener, removeEventListener, handleNFTRequest]);

  useEffect(() => {
    addEventListener("importNft", handleNftImport);
    return () => {
      removeEventListener("importNft", handleNftImport);
    };
  }, [addEventListener, removeEventListener, handleNftImport]);

  const determineAndSetOrientation = () => {
    if (!mobileCheck() || !isLoaded) return;
    let width = window.innerWidth;
    let height = window.innerHeight;
    if (width < height) {
      setOrientation('PORTRAIT');
      requestFullscreen(false);
    } else {
      setOrientation('LANDSCAPE');
    }
  }

  useEffect(() => {
    determineAndSetOrientation();
    window.addEventListener('resize', determineAndSetOrientation);

    return () => {
      window.removeEventListener('resize', determineAndSetOrientation);
    };
  }, [isLoaded, mobileCheck]);

  return (
    <main>
      <div id='unity-container' className={!isLoaded ? "loading-background" : ""}>
        {mobileCheck() && orientation == "PORTRAIT" &&(
            <div>
                <img alt='rotate' src='./rotate.png' className="rotate-icon" />
                <p className="info-text">Please rotate device to landscape.</p>
            </div>
        )}
        {mobileCheck() && orientation == "LANDSCAPE" &&
            <div id="fullscreenMask" onClick={() => {requestFullscreen(true)}}/>
        }
        {!isLoaded && (
            <div className='loading-container'>
                <div className='loading'>
                    <div id="progressbar">
                        <div style={{width: Math.round(loadingProgression * 100) + "%"}}></div>
                    </div>
                    <p className="info-text">Loading assets... {Math.round(loadingProgression * 100)}%</p>
                    <p className="info-text" style={{padding: 10}}>First time loading may take a few minutes.</p>
                </div> 
            </div>
        )}

        <Unity
            style={{ visibility: isLoaded && orientation == "LANDSCAPE" ? "visible" : "hidden" }}
            unityProvider={unityProvider}
            devicePixelRatio={devicePixelRatio}
        />
      </div>
    </main>
  );
}

export default App;
